
#app {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  opacity: 0;
  -webkit-animation: 1s fadeIn 0s ease both;
  animation: 1s fadeIn 0s ease both;
  font-size: 0.24rem;
}
@-webkit-keyframes fadeIn{
from{opacity: 0;
}
to{opacity: 1;
}
}
@keyframes fadeIn{
from{opacity: 0;
}
to{opacity: 1;
}
}
.v-enter{
  opacity: 0;
}
.v-enter-active{
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.v-enter-to{
  opacity: 1;
}
.v-leave{
  opacity: 1;
}
.v-leave-to{
  opacity:0;
}
.v-leave-active{
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

